body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  height: calc(100vh + 5em);
  width: 100vw;
  overflow: hidden;
}

.content {
  height: 100vh;
  margin-top: 1em;
}

@media screen and (min-width: 765px) {
  .app {
    height: 100vh;
  }
}



